<template>
    <div class="box">
        <el-card class="box-card">
            <el-card class="floor-num" shadow="always">
                <div>
                    <i class="el-icon-caret-top" v-show="this.going_up"></i>
                    <i
                        class="el-icon-caret-top disabled"
                        v-show="!this.going_up"
                    ></i>
                    {{ this.current_floor }}
                    <i class="el-icon-caret-bottom" v-show="!this.going_up"></i>
                    <i
                        class="el-icon-caret-bottom disabled"
                        v-show="this.going_up"
                    ></i>
                </div>
            </el-card>
            <div class="button-group">
                <div
                    v-for="o in this.floor_count"
                    :key="o"
                    class="button-place"
                >
                    <el-button
                        type="primary"
                        plain
                        class="button"
                        @click="handleInsideButtonClick(o)"
                        :class="{ 'button-click': button_click[o] }"
                    >
                        {{ o }}
                    </el-button>
                </div>
            </div>
            <div class="control">
                <el-switch
                    class="switch"
                    style="display: block"
                    v-model="door"
                    active-color="#13ce66"
                    inactive-color="#ff4949"
                    active-text="ÂºÄÈó®"
                    inactive-text="ÂÖ≥Èó®"
                    @click.native="doorAlert()"
                >
                </el-switch>
                <el-button
                    @click="handlePhoneClick()"
                    type="warning"
                    icon="el-icon-phone"
                    circle
                ></el-button>
                <el-button
                    @click="handleBellClick()"
                    type="danger"
                    icon="el-icon-message-solid"
                    circle
                ></el-button>
            </div>
        </el-card>
    </div>
</template>

<script>
export default {
    name: "Inside",
    data() {
        return {
            button_click: [],
            timer: null,
            running: false, //ÊòØÂê¶Âú®ËøêË°å
            going_up: true, //trueÂêë‰∏ä falseÂêë‰∏ã
            door: false, //Èó®ÁöÑÂºÄÂÖ≥Áä∂ÊÄÅ
            current_floor: 1, //ÂΩìÂâçÊ•ºÂ±Ç
            max_floor: 20,
            min_floor: 1,
            floor_id: [],
            outside_up: [], //Â§ñÈÉ®ÁîµÊ¢ØÂêë‰∏äËØ∑Ê±ÇÈòüÂàó
            outside_down: [], //Â§ñÈÉ®ÁîµÊ¢ØÂêë‰∏ãËØ∑Ê±ÇÈòüÂàó
            inside: [], //ÂÜÖÈÉ®ÂëºÊ¢Ø‰ø°Âè∑
            call: [] //Â≠òÊîæÂÜÖÂ§ñÊâÄÊúâÂëºÊ¢ØÂ±Ç
        }
    },
    props: {
        ele_id: Number, //ÁîµÊ¢ØÁºñÂè∑
        floor_count: Number, //ÊÄªÊ•ºÂ±ÇÊï∞
        call_ele_id: Number,
        press_floor: Number, //Áà∂ÁªÑ‰ª∂ÁöÑÊåâ‰∏ãÊ•ºÂ±Ç
        up: Boolean, //ÊòØÂêë‰∏ä
        down: Boolean
    },
    methods: {
        handlePhoneClick() {
            alert("‚òéÔ∏èËØ∑Ê±ÇÈÄöËØùÔºÅ‚òéÔ∏è")
        },
        handleBellClick() {
            alert("üÜòËØ∑Ê±ÇÊîØÊè¥!üÜò")
        },
        //Êõ¥Êñ∞‰∏ä‰∏ãÊ•ºÁöÑÊó∂ÂÄôÁöÑÂÜÖÈÉ®ÊåáÁ§∫ÁÅØ
        updateFloorInfo() {
            //ÊØèÁßíÂêëÁà∂ÁªÑ‰ª∂‰º†ÈÄíËØ•ÁîµÊ¢ØÂΩìÂâçÁöÑidÂíåÊ•ºÂ±Ç
            //Êé•ÂèóÁà∂ÁªÑ‰ª∂‰º†ÂÄºÂõûÊù•
            this.$emit(
                "childStatus",
                this.ele_id,
                this.current_floor,
                this.running,
                this.call,
                this.going_up
            )
        },
        //Êõ¥Êñ∞outside
        updateOutsideUp(floor) {
            this.$emit("updateOutsideUp", floor)
        },
        updateOutsideDown(floor) {
            this.$emit("updateOutsideDown", floor)
        },
        //ÂÜÖÈÉ®ÊåâÈíÆÂëºÊ¢Ø
        handleInsideButtonClick(floor) {
            this.button_click[floor] = true
            //floorÊòØÂëºÊ¢ØÊ•ºÂ±Ç,ÊääÂÜÖÈÉ®ÂëºÊ¢Ø‰ø°Âè∑ÊîπÊàê1ÔºåÂä†ÂÖ•ÂëºÊ¢ØÈòüÂàó
            this.inside[floor] = 1
            console.log("ÂÜÖÈÉ®ÊåâÈîÆÂØπÂàóÊàëÂ∫∑Â∫∑", this.inside)
            this.dial(floor)
        },
        //Â§ñÈÉ®button click
        outsideButtonClick(floor) {
            this.outside[floor] = 1
            this.dial(floor)
        },
        //blogÈáåÈù¢ËØ¥ÔºåÁî®‰∏Ä‰∏™ÊåâÈíÆÂáΩÊï∞Â∞±ÂèØ‰ª•
        dial(floor) {
            // if (this.call.indexOf(floor) < 0) {
            console.log("Áé∞Âú®Êåâ‰∏ãÁ¨¨Âá†Â±ÇÔºü", floor)
            this.call.push(floor)
            this.call.sort()
            console.log("Áé∞Âú®ÁöÑÈòüÂàóÊòØÔºö", this.call)
            if (!this.running) {
                this.checkStatus()
            }
            // }
        },
        //ÊâæÊúÄÂ∞èÊúÄÂ§ßÂÄº
        getMinInQueue(arr) {
            if (arr.length <= 0) {
                console.log("Êâæ‰∏çÂà∞ÊúÄÂ∞èÂÄº")
                return false
            }
            if (arr.length == 1) {
                return arr[0]
            } else {
                var min = arr[0]
                for (var i in arr) {
                    if (arr[i] < min) {
                        min = arr[i]
                    }
                }
                return min
            }
        },
        getMaxInQueue(arr) {
            if (arr.length <= 0) {
                console.log("Êâæ‰∏çÂà∞ÊúÄÂ§ßÂÄº")
                return false
            }
            if (arr.length == 1) {
                return arr[0]
            } else {
                var max = arr[0]
                for (var i in arr) {
                    if (arr[i] > max) {
                        max = arr[i]
                    }
                }
                return max
            }
        },
        //Âú®ÈòüÂàó‰∏≠Âà†Èô§ÊåáÂÆöÊ•ºÂ±Ç
        removeFromQueue(queue, floor) {
            console.log("Ë¶ÅÂà†Èô§ÁöÑÊï∞Â≠óÊòØÔºö" + floor)
            var index = queue.indexOf(floor)
            if (index > -1) {
                queue.splice(index, 1)
            }
        },
        //ÂºÄÂÖ≥Èó®
        openDoor() {
            this.door = true
        },
        closeDoor() {
            this.door = false
            console.log("door closed")
        },
        //ÂºÄÈó®Ë≠¶Âëä
        doorAlert() {
            if (this.running) {
                alert("ËøêË°åÊúüÈó¥Á¶ÅÊ≠¢ÂºÄÈó®ÔºÅ")
                this.door = false
            }
        },
        //Âà§Êñ≠Áä∂ÊÄÅÔºåÊØèÈöî‰∏ÄÊÆµÊó∂Èó¥ÊâßË°å
        checkStatus() {
            //Âà§Êñ≠ÂëºÂè´ÈòüÂàóÊòØÂê¶ËøòÊúâ‰∏úË•øÔºå‰øÆÊîπËøêË°åÁä∂ÊÄÅ
            this.running = this.call.length > 0 ? true : false
            //ÊòØÂê¶Âú®Â∫ïÂ±Ç
            if (this.current_floor == this.min_floor) {
                this.going_up = true
                //ÊòØÂê¶Âú®È°∂Â±Ç
            } else if (this.current_floor == this.max_floor) {
                this.going_up = false
            } else {
                //Âú®‰∏≠Èó¥Â±ÇÁöÑÁä∂ÊÄÅÂà§Êñ≠
                this.going_up &&
                (!this.running ||
                    this.current_floor <= this.getMaxInQueue(this.call))
                    ? (this.going_up = true)
                    : (this.going_up = false)
                !this.going_up &&
                (!this.running ||
                    this.current_floor >= this.getMinInQueue(this.call))
                    ? (this.going_up = false)
                    : (this.going_up = true)
                console.log(this.getMaxInQueue(this.call), this.going_up)
            }
        },
        //ÊääÂ§ñÈù¢ÁöÑÂä†ËøõÊù•
        addOutside(up, down, press_floor) {
            console.log("Áà∂ÁªÑ‰ª∂Ëß¶ÂèëÊàë‰∫Ü,ÊàëÊòØÁîµÊ¢Ø", this.ele_id)
            console.log("Áé∞Âú®Êåâ‰∏ãÁ¨¨Âá†Â±ÇÔºü", press_floor)
            if (up == true) {
                console.log("Êåâ‰∏ã‰∫ÜÂêë‰∏äÊåâÈîÆ")
                this.outside_up[press_floor] = 1
            } else if (down == true) {
                console.log("Êåâ‰∏ã‰∫ÜÂêë‰∏ãÊåâÈîÆ")
                this.outside_down[press_floor] = 1
            }
            this.dial(press_floor)
        },
        run() {
            var need_stop = false
            if (this.running) {
                if (this.call.indexOf(this.current_floor) > -1) {
                    //Âà∞ËææÂÜÖÈÉ®ÂëºÂè´Ê•ºÂ±Ç
                    if (this.inside[this.current_floor] == 1) {
                        this.removeFromQueue(this.call, this.current_floor)
                        this.inside[this.current_floor] = 0
                        this.button_click[this.current_floor] = 0
                        need_stop = true
                    }
                    if (this.going_up) {
                        if (this.outside_up[this.current_floor] == 1) {
                            console.log("Âà∞ËææÂ§ñÈù¢ÂëºÂè´Ôºö", this.current_floor)
                            this.removeFromQueue(this.call, this.current_floor)
                            this.outside_up[this.current_floor] = 0
                            this.updateOutsideUp(this.current_floor)
                            need_stop = true
                        }
                        if (
                            this.outside_down[this.current_floor] == 1 &&
                            this.current_floor == this.getMaxInQueue(this.call)
                        ) {
                            this.removeFromQueue(this.call, this.current_floor)
                            this.outside_down[this.current_floor] = 0
                            this.updateOutsideDown(this.current_floor)
                            need_stop = true
                        }
                    } else {
                        if (this.outside_down[this.current_floor] == 1) {
                            this.removeFromQueue(this.call, this.current_floor)
                            this.outside_down[this.current_floor] = 0
                            this.updateOutsideDown(this.current_floor)
                            need_stop = true
                        }
                        if (
                            this.outside_up[this.current_floor] == 1 &&
                            this.current_floor == this.getMinInQueue(this.call)
                        ) {
                            this.removeFromQueue(this.call, this.current_floor)
                            this.outside_up[this.current_floor] = 0
                            this.updateOutsideUp(this.current_floor)
                            need_stop = true
                        }
                    }
                    if (need_stop) {
                        console.log("stop:", this.current_floor)
                        this.ding(this.current_floor)
                    } else {
                        this.going_up ? this.moveUp() : this.moveDown()
                    }
                } else {
                    this.going_up ? this.moveUp() : this.moveDown()
                    this.updateFloorInfo()
                }
                this.checkStatus()
            }
        },
        //ÊöÇÂÅúËÆ°Êó∂Âô®ÔºåÁÜÑÁÅ≠ËØ•Ê•ºÂ±ÇÁöÑÁÅØÂÖâ
        ding(floor) {
            let _this = this
            let that = this
            //ÈúÄË¶ÅÁîµÊ¢ØÂÅú‰∏ãÔºåÂ∞±ÊäätimerÊ∏ÖÁ©∫
            if (this.timer) {
                clearInterval(this.timer)
            }
            this.openDoor()
            //‰∏ç‰ºöÈáçÂ§çÊâßË°åÁöÑÂª∂Êó∂ÂáΩÊï∞
            setTimeout(function() {
                _this.closeDoor()
                setTimeout(function() {
                    that.timer = setInterval(that.run, 1000)
                }, 3000)
            }, 4000)
        },
        //‰∏äË°åÂáΩÊï∞
        moveUp() {
            if (this.current_floor < this.max_floor) {
                this.current_floor++
            }
        },
        //‰∏ãË°å
        moveDown() {
            if (this.current_floor > this.min_floor) {
                this.current_floor--
            }
        }
    },
    created() {
        //ÂàùÂßãÂåñÁîµÊ¢ØÁä∂ÊÄÅ
        for (var i = 0; i <= this.max_floor; ++i) {
            this.button_click[i] = false
            this.outside_up[i] = 0
            this.outside_down[i] = 0
            this.inside[i] = 0
            this.floor_id[i] = i //Ëøô‰∏™È°∫Â∫èÊòØ
        }
    },
    mounted() {
        //ÊØèÁßíÊâßË°å‰∏ÄÊ¨°runÂáΩÊï∞
        this.timer = setInterval(this.run, 1000)

        //‰∏ãÈù¢ËøôÊÆµ‰ª£Á†ÅÊàë‰πüÁúã‰∏çÂ§™ÊáÇÔºå‰ΩÜÊòØÂÆûÁé∞‰∫ÜÂÆöÊúüÊõ¥Êñ∞
        // if (this.timer) {
        //     clearInterval(this.timer)
        // } else {
        //     this.timer = setInterval(() => {
        //         // Ë∞ÉÁî®Áõ∏Â∫îÁöÑÊé•Âè£ÔºåÊ∏≤ÊüìÊï∞ÊçÆ
        //         //‰ªñÊÑèÊÄùÂ§ßÊ¶ÇÂ∞±ÊòØÈöî‰∏ÄÊÆµÊó∂Èó¥‰Ω†Ë¶ÅÊ£ÄÊµã‰∏Ä‰∏ãÂΩìÂâçÊ•ºÂ±ÇÔºåÂ§ñÈÉ®ËØ∑Ê±ÇËøô‰∫õ
        //         //ËøôÈáåÂ∞±ÊòØÊ£ÄÊµãÁî®ÁöÑÔºÅ
        //         // console.log(this.call)
        //         //‰∏äË°åor‰∏ãË°å
        //         if (this.running && this.going_up) {
        //             this.current_floor++
        //         } else if (this.running && !this.going_up) {
        //             this.current_floor--
        //         }
        //         this.checkStatus()
        //     }, 3000)
        // }
    },
    destroyed() {
        clearInterval(this.timer)
    }
}
</script>

<style lang="stylus" scoped>

.box-card {
    width: 340px;
    height: 800px;
    padding:10px

    .floor-num {
        background-color: #409eff;
        text-align: center;
        font-size: 150%
        color: #ffffff;
        margin-bottom: 20px;

        .el-icon-caret-top{
            margin-right:40px
        }

        .el-icon-caret-bottom{
            margin-left:40px
        }

        .disabled{
            color: #152959
        }

    }

    .button-group{
        width: 100%
        overflow: hidden;
        height: 0
        padding-bottom: 200%
        display: flex;
        flex-wrap: wrap;

        .button-place{
            width: 50%
            display: flex;
            justify-content: center;
            margin-bottom: 20px

            .button{
                width:120px
            }
            .button-click{
                color:#ffffff
                background: #409eff
            }
        }

    }

    .control{
        display: flex;
        justify-content: space-around;
        align-items: center;

        .switch{
            margin-left: 20px;
        }
    }


}
</style>



