<template>
    <div>
        <div>
            <el-tag class="number-tag">{{ this.floor_id }}</el-tag>
            <el-button-group>
                <el-button
                    size="small"
                    type="primary"
                    @click="handleUpClick()"
                    :class="{ 'button-click': this.up }"
                    ><i class="el-icon-top"></i
                ></el-button>
                <el-button
                    size="small"
                    type="primary"
                    @click="handleDownClick()"
                    :class="{ 'button-click': this.down }"
                    ><i class="el-icon-bottom"></i
                ></el-button>
            </el-button-group>
        </div>
    </div>
</template>

<script>
export default {
    name: "Outside",
    props: {
        floor_id: Number //ÊåâÈíÆÂØπÂ∫îÊ•ºÂ±Ç
    },
    data() {
        return {
            //ÂΩìÂâçÁöÑ‰∏ä‰∏ãÈîÆÊòØÂê¶Â∑≤ÁªèÊåâ‰∫Æ
            up: false,
            down: false
        }
    },
    methods: {
        //ÁÇπÂáª‰∏ä„ÄÅ‰∏ãÊåâÈíÆÔºåÊääËØ•Ê•ºÂ±ÇÂä†ÂÖ•Ë∑ùÁ¶ªËØ•Ê•ºÂ±ÇÊúÄËøëÁöÑÁîµÊ¢ØÁöÑÁ≠âÂæÖÈòüÂàó‰∏≠
        handleUpClick() {
            if (!this.up) {
                //ÊääÂΩìÂâçÂëºÂè´Ê•ºÂ±Ç‰º†ÁªôÁà∂ÁªÑ‰ª∂
                this.up = true
                this.$emit("childCallFloorUp", this.floor_id)
            }
        },
        handleDownClick() {
            if (!this.down) {
                //ÊääÂΩìÂâçÂëºÂè´Ê•ºÂ±Ç‰º†ÁªôÁà∂ÁªÑ‰ª∂
                this.down = true
                this.$emit("childCallFloorDown", this.floor_id)
            }
        },
        upStateRestoration() {
            console.log(this.floor_id, "ÂèòËâ≤")
            this.up = false
        },
        downStateRestoration() {
            this.down = false
        }
    }
}
</script>

<style>
.number-tag {
    width: 50px;
    text-align: center;
    margin: 20px auto 20px auto;
}

.button-click {
    background: #b5d2ef;
}
</style>



