<template>
    <div class="page">
        <div>
            <inside
                v-for="o in this.ele_count"
                :key="o"
                class="card"
                :floor_count="floor_count"
                :ele_id="o"
                :press_floor="press_floor"
                :up="up"
                :down="down"
                :call_ele_id="call_ele_id"
                @childStatus="getChildStatus"
                @updateOutsideDown="updateOutsideDown"
                @updateOutsideUp="updateOutsideUp"
                ref="insidechild"
            ></inside>
        </div>
        <div>
            <outside
                class="floor"
                v-for="o in this.floor_count"
                :key="o"
                :floor_id="o"
                @childCallFloorUp="callFloorUp"
                @childCallFloorDown="callFloorDown"
                ref="outsideChild"
            ></outside>
        </div>
    </div>
</template>

<script>
import Inside from "./components/inside"
import Outside from "./components/outside"
export default {
    name: "Home",
    components: {
        Inside,
        Outside
    },
    data() {
        return {
            ele_count: 5, //ÁîµÊ¢ØÊï∞Èáè,ÂØπÂ∫îÁîµÊ¢Øid
            floor_count: 20, //Ê•ºÂ±ÇÊï∞Èáè
            running: [], //ÁîµÊ¢ØËøêË°åÁä∂ÊÄÅ
            going_up: [], //ÁîµÊ¢ØÊñπÂêë
            current_floor: [], //ÊØè‰∏™ÁîµÊ¢ØÁöÑÂΩìÂâçÊ•ºÂ±Ç
            call: [[]], //ÊØè‰∏™ÁîµÊ¢ØÁöÑÁ≠âÂæÖÈòüÂàó
            //‰º†ÁªôÂ≠êÁªÑ‰ª∂ÁöÑÂÄº
            //Ë¶ÅÁª¥Êä§‰∏Ä‰∏™ÂèòÈáè ‰øùËØÅÊ•ºÂ±ÇË¢´Êåâ‰∏ãÊó∂ÂÄôËÉΩË¢´ÁõëÂê¨
            press_floor: null,
            call_ele_id: 1, //Ë¶ÅÊîæÂÖ•ÁöÑele_id
            up: false, //‚¨Ü‰∏äË¢´Êåâ‰∏ã
            down: false //‰∏ãË¢´Êåâ‰∏ã
        }
    },
    methods: {
        // ËÆ°ÁÆóÂ∑≤ÊéíÂ∫èÁöÑÈòüÂàó[n]‰∏≠aÂíåb‰πãÈó¥ÁöÑÊï∞ÁõÆËÆ°Êï∞
        // a > b or a < b is ok
        betweenCount(a, b, id) {
            if (a > b) {
                var temp = a
                a = b
                b = temp
            }
            console.log(this.call[id])
            var betweenNum = []
            for (var i in this.call[id]) {
                if (
                    this.call[id][i] <= b &&
                    this.call[id][i] >= a &&
                    betweenNum.indexOf(this.call[id][i] < 0)
                ) {
                    betweenNum.push(this.call[id][i])
                }
            }
            console.log("between:", betweenNum.length)
            return betweenNum.length
        }, //ÊâæÊúÄÂ∞èÊúÄÂ§ßÂÄº
        getMinInQueue(arr) {
            // if (length.arr) {
            return arr[0]
            // }
        },
        getMaxInQueue(arr) {
            // if (length.arr) {
            return arr[arr.length - 1]
            // }
        },
        //ËÆ°ÁÆóÂ∫îËØ•ÊîæÂÖ•Âì™‰∏™ÁîµÊ¢Ø
        calculateUpDistance(press_floor) {
            var maxDistance = 2 * this.floor_count //ÂèØËÉΩÂá∫Áé∞ÁöÑÊúÄÂ∞èË∑ùÁ¶ª
            var distance = 0
            var elevatorToPush = 1
            for (var i = 0; i < this.ele_count; i++) {
                if (!this.running[i]) {
                    distance = Math.abs(press_floor - this.current_floor[i])
                } else {
                    var minInQueue = this.getMinInQueue(this.call[i]) //ÊâæÂá∫ÂëºÂè´ÈòüÂàó‰∏≠ÊúÄÂ∞èÁöÑ
                    var maxInQueue = this.getMaxInQueue(this.call[i]) //ÊâæÂá∫ÂëºÂè´ÈòüÂàó‰∏≠ÊúÄÂ§ßÁöÑ
                    if (this.current_floor[i] <= press_floor) {
                        if (this.going_up[i]) {
                            console.log("ÁîµÊ¢ØÂêë‰∏äÔºåÊåâÈîÆÊØîÂΩìÂâçÈ´ò")
                            distance =
                                press_floor -
                                this.current_floor[i] +
                                5 *
                                    this.betweenCount(
                                        press_floor,
                                        this.current_floor[i],
                                        i
                                    )
                        } else {
                            console.log("ÁîµÊ¢ØÂêë‰∏ãÔºåÊåâÈîÆÊØîÂΩìÂâçÈ´ò")
                            distance =
                                this.current_floor[i] -
                                minInQueue +
                                press_floor -
                                minInQueue +
                                5 *
                                    this.betweenCount(
                                        minInQueue,
                                        press_floor,
                                        i
                                    )
                        }
                    } else {
                        if (this.going_up[i]) {
                            console.log("ÁîµÊ¢ØÂêë‰∏äÔºåÊåâÈîÆÊØîÂΩìÂâç‰Ωé")
                            distance =
                                maxInQueue -
                                this.current_floor[i] +
                                maxInQueue -
                                minInQueue +
                                Math.abs(minInQueue - press_floor) +
                                5 *
                                    this.betweenCount(
                                        maxDistance,
                                        maxInQueue,
                                        i
                                    )
                            console.log(
                                "distance:",
                                distance,
                                "max:",
                                maxDistance
                            )
                        } else {
                            console.log("ÁîµÊ¢ØÂêë‰∏ãÔºåÊåâÈîÆÊØîÂΩìÂâç‰Ωé")
                            distance =
                                this.current_floor[i] -
                                minInQueue +
                                Math.abs(minInQueue - press_floor) +
                                5 *
                                    this.betweenCount(
                                        this.current_floor[i],
                                        minInQueue,
                                        i
                                    )
                        }
                    }
                    if (distance < maxDistance) {
                        maxDistance = distance
                        elevatorToPush = i + 1
                    }
                }
            }
            console.log(
                press_floor + "‰∏äÊ•ºÂ∞ÜË¶ÅÊîæÂÖ•Á¨¨" + elevatorToPush + "ÁîµÊ¢Ø"
            )
            this.up = true
            return elevatorToPush //ÂæóÂà∞Êåâ‰∏ãÊåâÈîÆÁöÑÊ•ºÂ±ÇÂç≥Â∞ÜÊîæÂÖ•Âì™‰∏™ÁîµÊ¢Ø
        },
        calculateDownDistance(press_floor) {
            var maxDistance = 2 * this.floor_count //ÂèØËÉΩÂá∫Áé∞ÁöÑÊúÄÂ∞èË∑ùÁ¶ª
            var distance = 0
            var elevatorToPush = 1
            for (var i = 0; i < this.ele_count; i++) {
                if (!this.running[i]) {
                    distance = Math.abs(press_floor - this.current_floor[i])
                } else {
                    var minInQueue = this.getMinInQueue(this.call[i]) //ÊâæÂá∫ÂëºÂè´ÈòüÂàó‰∏≠ÊúÄÂ∞èÁöÑ
                    var maxInQueue = this.getMaxInQueue(this.call[i]) //ÊâæÂá∫ÂëºÂè´ÈòüÂàó‰∏≠ÊúÄÂ§ßÁöÑ
                    if (this.current_floor[i] <= press_floor) {
                        if (this.going_up[i]) {
                            console.log("ÁîµÊ¢ØÂêë‰∏äÔºåÊåâÈîÆÊØîÂΩìÂâçÈ´ò")
                            distance =
                                maxInQueue -
                                this.current_floor[i] +
                                Math.abs(maxInQueue - press_floor) +
                                5 *
                                    this.betweenCount(
                                        this.current_floor[i],
                                        maxInQueue,
                                        i
                                    )
                        } else {
                            console.log("ÁîµÊ¢ØÂêë‰∏ãÔºåÊåâÈîÆÊØîÂΩìÂâçÈ´ò")
                            distance =
                                this.current_floor[i] -
                                minInQueue +
                                maxInQueue -
                                minInQueue +
                                Math.abs(maxInQueue - press_floor) +
                                5 *
                                    this.betweenCount(
                                        maxDistance,
                                        maxInQueue,
                                        i
                                    )
                        }
                    } else {
                        if (this.going_up[i]) {
                            console.log("ÁîµÊ¢ØÂêë‰∏äÔºåÊåâÈîÆÊØîÂΩìÂâç‰Ωé")
                            distance =
                                maxInQueue -
                                this.current_floor[i] +
                                maxInQueue -
                                press_floor +
                                5 *
                                    this.betweenCount(
                                        maxInQueue,
                                        press_floor,
                                        i
                                    )
                            console.log(
                                "distance:",
                                distance,
                                "max:",
                                maxDistance
                            )
                        } else {
                            console.log("ÁîµÊ¢ØÂêë‰∏ãÔºåÊåâÈîÆÊØîÂΩìÂâç‰Ωé")
                            distance =
                                this.current_floor[i] -
                                press_floor +
                                5 *
                                    betweenCount(
                                        press_floor,
                                        this.current_floor[i],
                                        i
                                    )
                        }
                    }
                    if (distance < maxDistance) {
                        maxDistance = distance
                        elevatorToPush = i + 1
                    }
                }
            }
            console.log(
                press_floor + "‰∏ãÊ•ºÂ∞ÜË¶ÅÊîæÂÖ•Á¨¨" + elevatorToPush + "ÁîµÊ¢Ø"
            )
            this.down = true
            return elevatorToPush //ÂæóÂà∞Êåâ‰∏ãÊåâÈîÆÁöÑÊ•ºÂ±ÇÂç≥Â∞ÜÊîæÂÖ•Âì™‰∏™ÁîµÊ¢Ø
        },
        //ÂæóÂà∞ÂΩìÂâçÂì™‰∏™Ê•ºÂ±ÇË¢´Â§ñÈÉ®ÂëºÂè´,ÊääËøô‰∏™ÂëºÂè´‰ø°Âè∑‰º†ÈÄíÁªôÊúÄËøëÁöÑÁîµÊ¢Ø
        callFloorUp(floor_num) {
            this.press_floor = floor_num
            //ÊääÂΩìÂâçÈúÄË¶ÅÂä†ÂÖ•ÁöÑÁîµÊ¢ØID„ÄÅÊ•ºÂ±Ç‰ª•Âèä‰∏ä‰∏ãÈÉΩ‰º†ÁªôÂ≠êÁªÑ‰ª∂
            this.call_ele_id = this.calculateUpDistance(floor_num)
            //ÊääÁä∂ÊÄÅ‰º†ÈÄíÁªôÂìçÂ∫îÁîµÊ¢ØÂ≠êÁªÑ‰ª∂
            this.$refs.insidechild[this.call_ele_id - 1].addOutside(
                this.up,
                this.down,
                floor_num
            )
            console.log(
                "‰º†ÂÖ•Á¨¨Âá†‰∏™ÁîµÊ¢ØÔºö",
                this.call_ele_id,
                "Á¨¨Âá†Â±ÇÊ•º",
                this.press_floor
            )

            for (var i = 0; i < this.ele_count; i++) {
                console.log("call:", this.call[i])
            }
        },
        callFloorDown(floor_num) {
            this.press_floor = floor_num
            this.call_ele_id = this.calculateDownDistance(floor_num)
            this.$refs.insidechild[this.call_ele_id - 1].addOutside(
                this.up,
                this.down,
                floor_num
            )
            //ÊµãËØïlog
            console.log(
                "‰º†ÂÖ•Á¨¨Âá†‰∏™ÁîµÊ¢ØÔºö",
                this.call_ele_id,
                "Á¨¨Âá†Â±ÇÊ•º",
                this.press_floor
            )
            for (var i = 0; i < this.ele_count; i++) {
                console.log("call:", this.call[i])
            }
        },
        //‰ªéinsideËé∑ÂèñÊØè‰∏™ÁîµÊ¢ØÁöÑÁä∂ÊÄÅ
        getChildStatus(
            id,
            child_current,
            child_running,
            child_queue,
            child_up
        ) {
            this.current_floor[id - 1] = child_current
            this.running[id - 1] = child_running
            this.call[id - 1] = child_queue
            this.going_up[id - 1] = child_up
            console.log("ele_id:", id)
        },
        //Êõ¥Êñ∞outside
        updateOutsideUp(floor) {
            this.$refs.outsideChild[floor - 1].upStateRestoration()
            console.log("Ê•ºÔºö", floor)
        },
        updateOutsideDown(floor) {
            this.$refs.outsideChild[floor - 1].downStateRestoration()
        }
    },
    created() {
        //ÂàùÂßãÂåñÁîµÊ¢ØÁä∂ÊÄÅ
        for (var i = 0; i < this.ele_count; ++i) {
            this.running[i] = false
            this.current_floor[i] = 1
        }
    }
}
</script>

<style>
.card {
    display: inline-flex;
    width: 370px;
    margin: 20px auto 20px auto;
}
.floor {
    display: inline-flex;
    justify-content: space-around;
    width: 370px;
}
</style>



